<template>
  <div class="around mt-5">
    <div class="content p-5 mx-auto">
      <div class="text-left mt-5">
        <div class="title-text text-left">集點店家</div>
        <div class="d-flex">
          <div class="p-3 bg-gray m-3" v-for="(item, index) in storeList" :key="index">
            <div @click="$router.push({name:'Arounddetail',query: { id:item.title, type: 'store'} })">{{ item.title }}</div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'around',
  data () {
    return {
      storeList: [
        { title: '全家', day: '11/2', content: 'whereList' },
        { title: '全家2222', day: '11/2', content: 'whereList' },
        { title: '全家3333', day: '11/2', content: 'whereList' }
      ]
    }
  },
  methods: {
  }
}
</script>

<style>

</style>



